import { Breadcrumb, Button, Col, Input, Row, Table } from 'antd'
import Search from 'antd/lib/transfer/search'
import React, { useEffect, useState } from 'react'
import { fetchFloatOrders } from '../../../services/api/float'
import { COMMENT_TYPES } from '../../../util/constants'
import { FLOAT_ORDERS_COLUMN } from '../../MyRequest/components/Float/List'


const columns = (props) => FLOAT_ORDERS_COLUMN.concat([

])

const AllFloats = (props) => {
  const [pagination, setPagination] = useState({currentPage: 0, total: 0, pageSize: 20})
  const [floats, setFloats] = useState([])
  const [loading, setLoading] = useState(false)
  const [selectedFloat, setSelectedFloat] = useState(null)
  const [visibility, setVisibility] = useState(false)
  

  const handleChange = () => {
    //resetPagination()
  }

  const fetchFloats = async (query) => {
    setLoading(true)
    const queryObj = {
      // toBeApproved: status === "toBeApproved",
      // approved: status === "approved",
      // reference: searchTerm ? searchTerm : null
      pageNumber: pagination?.currentPage,
      pageSize: pagination?.pageSize
    }
    try {
      const result = await fetchFloatOrders(queryObj)
      if(result?.meta) {
        const { currentPage, pageSize, total, totalPages } = result?.meta
        setPagination({...pagination, currentPage: currentPage + 1, total: total * totalPages, pageSize, totalPages})
      }
      setFloats(result?.data)
    } catch (error) {
      
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    fetchFloats({pageSize: pagination.pageSize, pageNo: pagination.currentPage})
  }, [])

  return (
    <>
      <Row>
        <Col span={8}>
          <Breadcrumb>
            <Breadcrumb.Item>
              ALL FLOATS
            </Breadcrumb.Item>
          </Breadcrumb>
        </Col>
        <Col span={16}>
          <div style={{display:'flex', flexDirection: 'row', justifyItems: 'flex-end'}}>
            <Search style={{flexGrow: 1}} />
            <Button type='default'>
              FILTER
            </Button>
          </div>
        </Col>
      </Row>
      <Row style={{marginTop: 10}}>
        <Col span={24}>
          <Table
            loading={loading}
            columns={columns({
              onView: row => {
                props.resetComment()
                props.fetchComments(row?.id, COMMENT_TYPES.FLOAT)
                setSelectedFloat(row)
                setVisibility(true)
              }
            })}
            dataSource={floats}
            size='small'
            rowKey='id'
            pagination={false}
          />
        </Col>
      </Row>
    </>
  )
}

export default AllFloats